.story {
  &__block {
    margin-top: -1px;
  }
  &__left {
    border-right: 1px solid var(--dark-color);
    width: 50%;
    display: flex;
    flex-direction: column;

    @include tablet {
      border: none;
      width: 100%;
    }
  }
  &__right {
    width: 50%;

    @include tablet {
      width: 100%;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: auto;
    padding: 50px 80px;
    font-weight: 400;
    font-size: 58px;
    line-height: 65px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--dark-color);
    text-align: center;

    @include small-tablet {
      font-size: 40px;
      line-height: 50px;
      padding: 20px;
    }
  }

  &__image {
    display: block;
  }
  &__content {
    background-image: url(../../img/story.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 135px 0;
    aspect-ratio: 1/1;

    @include tablet {
      padding: 170px 0;
      aspect-ratio: initial;
    }
  }
  &__text {
    margin: 0 auto;
    max-width: 390px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--dark-color);

    &:not(:last-child) {
      margin-bottom: 24px;
    }

    @include small-tablet {
      font-size: 14px;
      padding: 0 15px;
      line-height: 20px;
    }
  }
}
