.banner {
  margin-top: -1px;
  &__image {
    display: block;
    margin-top: -180px;
    width: 100%;

    @include tablet {
      margin-top: -115px;
    }
    @media (max-width: 900px) {
      margin-top: -100px;
    }
    @include small-tablet {
      margin-top: -45px;
    }
    @include mobile {
      margin-top: -40px;
    }
  }
}
