.footer {
  margin-top: -1px;
  &__content {
    padding: 20px 0;
    display: flex;
    justify-content: space-around;
    background-color: #cdfd3b;

    @include mobile {
      flex-direction: column;
      gap: 15px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--dark-color);

    @include mobile {
      line-height: 20px;
    }
  }
  &__link {
    transition: all 0.3s;
    &:hover {
      transform: scale(1.2);
    }
  }
}
