.card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 125px 22px 22px;
  padding-top: 100px;
  min-height: 658px;
  text-align: center;
  background-image: url(../../img/product-bg.svg);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;

  @include small-tablet {
    padding-top: 170px;
  }

  &__taste {
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--dark-color);
    margin-bottom: 14px;
  }
  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--dark-color);
    margin-bottom: 14px;

    @include tablet {
      font-size: 26px;
    }
  }
}
